
import { defineComponent, PropType } from "vue";
import scrollViews from "@/baseComponents/scrollViews/index.vue";
// 定义所需参数的接口
export interface containerProps {
  title: string;
  showToolBar: boolean;
}
export default defineComponent({
  name: "viewContainer",
  components: {
    scrollViews,
  },
  props: {
    // 用list来接受参数
    list: {
      type: Object as PropType<containerProps>,
      required: true,
    },
    showScroll: {
      type: Boolean,
      default: true,
    },
  },
});
