
import { computed, defineComponent, reactive, ref, toRef, watch } from "vue";

export default defineComponent({
  name: "baseInput",
  components: {},
  props: {
    label: String,
    type: {
      type: String,
      default: "text",
    },
    modelValue: String,
    reg: {
      type: RegExp,
      default: /\S/,
    },
    margin: {
      type: String,
      default: "5px",
    },
  },
  setup(props, context) {
    let currentValue = ref("");
    let ishover = ref(false);
    let errText = ref("");
    let timeId = 0;
    watch(currentValue, (newValue, oldValue) => {
      clearTimeout(timeId);
      context.emit("update:modelValue", newValue);
    });
    let error = ref<boolean>(false);
    function rules() {
      error.value = !props.reg.test(currentValue.value);
      if (error.value) {
        errText.value = "输入不合规！";
      }
    }
    return { currentValue, timeId, ishover, error, rules, errText };
  },
});
